import React, { useState, useEffect } from 'react';
import getBlockchain from './ethereum.js';

function App() {
  const [simpleStorage, setSimpleStorage] = useState(undefined);
  const [data, setData] = useState(undefined);
  // const [pool, setPool] = useState(undefined);

  useEffect(() => {
    const init = async () => {
      const { simpleStorage } = await getBlockchain();
      const data = await simpleStorage.readPool();
      // const pool = await simpleStorage.readPool();
      setSimpleStorage(simpleStorage);
      setData(data);
      // setPool(pool);
    };
    init();
  }, []);

  const updateData = async e => {
    e.preventDefault();
    const data = e.target.elements[0].value;
    const tx = await simpleStorage.updateData(data);
    await tx.wait();
    const newData = await simpleStorage.readPool();
    setData(newData);
  };

  const guessData = async e => {
    e.preventDefault();
    const data = e.target.elements[0].value;
    const tx = await simpleStorage.guessData(data);
    await tx.wait();
    const newData = await simpleStorage.readPool();
    setData(newData);
  };

  if(
    typeof simpleStorage === 'undefined'
    || typeof data === 'undefined'
  ) {
    return 'Loading...';
  }

  return (
    <div className='container'>
      <div className='row'>

        <div className='col-sm-6'>
          <h2>Jackpot:</h2>
          <p>{(data/1000000000000000000).toString()}</p>
        </div>
{/* 
        <div className='col-sm-6'>
          <h2>Pool:</h2>
          <p>{pool}</p>
        </div> */}

        <div className='col-sm-6'>
          <h2>Guess number for 1 BUSD</h2>
          <form className="form-inline" onSubmit={e => guessData(e)}>
            <input 
              type="text" 
              className="form-control" 
              placeholder="data"
            />
            <button 
              type="submit" 
              className="btn btn-primary"
            >
              Submit
            </button>
          </form>
        </div>


        <div className='col-sm-6'>
          <h2>Change number</h2>
          <form className="form-inline" onSubmit={e => updateData(e)}>
            <input 
              type="text" 
              className="form-control" 
              placeholder="data"
            />
            <button 
              type="submit" 
              className="btn btn-primary"
            >
              Submit
            </button>
          </form>
        </div>

      </div>
    </div>
  );
}

export default App;

